import Vue from 'vue'
import moment from 'moment'
import 'moment/locale/zh-cn'
moment.locale('zh-cn')

// 
Vue.filter('fromNow', (time) => moment(time).fromNow())

// 类别文字转换
Vue.filter('tabTextTransform', (tab) =>
  tab === 'share' ? '分享' : tab === 'ask' ? '回答' :
    tab === 'job' ? '招聘' : '客户端测试')
